<template>
  <div class="ai-page">
    <div class="ai-chat-container">
      <AiAgentButton 
        :with-button="false"
        :is-open-prop="true"
        :is-absolute="false"
        :is-full-screen="false"
        :max-height="maxChatHeight"
        :z-index="10"
      />
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onUnmounted } from 'vue'
import AiAgentButton from '@/components/ai/AiAgentButton.vue'

export default {
  name: 'AiPage',
  components: {
    AiAgentButton
  },
  setup() {
    const maxChatHeight = ref('700px')

    const calculateMaxHeight = () => {
      // Получаем высоту экрана
      const screenHeight = window.innerHeight
      
      // Если высота экрана больше 1500px, ограничиваем чат до 1200px
      if (screenHeight > 1500) {
        maxChatHeight.value = '1200px'
      } else {
        // Вычитаем padding страницы (20px + 30px = 50px)
        maxChatHeight.value = 'calc(100vh - 50px)'
      }
      
      // Отладка
      console.log('Screen height:', screenHeight)
      console.log('Max chat height set to:', maxChatHeight.value)
    }

    onMounted(() => {
      calculateMaxHeight()
      window.addEventListener('resize', calculateMaxHeight)
    })

    onUnmounted(() => {
      window.removeEventListener('resize', calculateMaxHeight)
    })

    return {
      maxChatHeight
    }
  }
}
</script>

